<template>
  <div class="category-tiles container container--wide">
    <h2
      v-if="liquid.title"
      :id="`section-title-${liquid.sectionId}`"
      class="h4 category-tiles__heading"
      v-text="liquid.title"
    />
    <swiper
      :modules="modules"
      :autoplay="autoplaySettings"
      :navigation="swiper.navigationButtons"
      :breakpoints="swiper.breakpoints"
      :slides-per-view="swiper.slidesPerView"
      :center-insufficient-slides="swiper.centerInsufficientSlides"
      class="category-tiles__carousel"
    >
      <div
        class="swiper-button-prev swiper-button-custom"
      >
        <chevron-left />
      </div>
      <div
        class="swiper-button-next swiper-button-custom"
      >
        <chevron-right />
      </div>
      <template v-for="block in liquid.blocks">
        <template v-if="getBlockAttributes(block)">
          <swiper-slide
            :id="block.id"
            :key="block.id"
            tag="a"
            :class="[
              'collection-card',
              'lazyload',
              'swiper-slide',
              block.contentAlignment
            ]"
            :href="block.link"
            :aria-label="block.ariaLabel?.length ? block.ariaLabel : block.ctaText"
          >
            <div class="collection-card__media">
              <template v-if="block.videoLink">
                <video
                  class="collection-card__video"
                  autoplay="1"
                  loop="1"
                  muted="1"
                  playsinline="1"
                  preload="1"
                  js-category-tiles="video"
                  :data-link="block.link"
                >
                  <source
                    :src="block.video_url"
                    type="video/mp4"
                  >
                </video>
              </template>
              <template v-else>
                <div class="collection-card__primary-image">
                  <responsive-image
                    :alt="block.image.alt"
                    block-class="responsive-image__image blur-up lazyautosizes ls-is-cached lazyloaded"
                    :image="block.image"
                    :min-max="block.image.minMax"
                    :lazyload="true"
                  />
                </div>
                <div
                  v-if="block.hoverImage"
                  class="collection-card__secondary-image"
                >
                  <responsive-image
                    :alt="block.hoverImage.alt"
                    block-class="responsive-image__wrapper "
                    :image="block.hoverImage"
                    :min-max="block.hoverImage.minMax"
                  />
                </div>
                <div class="collection-card__body">
                  <div
                    v-if="block.link"
                    class="collection-card__cta text-link"
                  >
                    <span>{{ block.ctaText }}</span>
                    <arrow-up-right />
                  </div>
                </div>
              </template>
            </div>
          </swiper-slide>
        </template>
      </template>
    </swiper>
  </div>
</template>

<script>

/**
 * Vue: category-tiles (category-tiles)
 * -----------------------------------------------------------------------------
 * Gallery slider.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Autoplay, Navigation } from 'swiper'
import ArrowUpRight from '~icons/direction/arrow/up-right.svg'
import ChevronLeft from '~icons/direction/arrow/chevron-left.svg'
import ChevronRight from '~icons/direction/arrow/chevron-right.svg'

export default {
  name: 'CategoryTiles',

  components: {
    Swiper,
    SwiperSlide,
    ArrowUpRight,
    ChevronLeft,
    ChevronRight,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },

    settings: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
  },

  setup() {
    return {
      modules: [Autoplay, Navigation],
    }
  },

  data() {
    return {
      swiper: {
        slidesPerView: 'auto',
        centerInsufficientSlides: true,
        breakpoints: {
          '0': {
            spaceBetween: 16,
          },
          '768': {
            spaceBetween: 32,
          },
        },
        navigationButtons: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },
    }
  },

  computed: {

    /**
     * Carousel autoplay settings.
     * @returns {Null|Object}
     */
    autoplaySettings() {
      const { carousel_autoplay } = this.settings
      if(!carousel_autoplay) return

      return { delay: carousel_autoplay }
    },
  },

  methods: {
    getBlockAttributes(block) {
      if(!block.link || block.image && block.videoLink) return false
      return true
    },
  },
}
</script>

<style lang="scss">
  @import 'swiper/css';
  @import './category-tiles';
</style>
